'use client';

import { useState, useEffect, useRef } from 'react';
import type { VideoSource } from '@/lib/api/video-api';
import { useRouter, useSearchParams } from 'next/navigation';
import { ArrowLeft } from 'lucide-react';

interface VideoPlayerProps {
  mediaId: number;
  mediaType: 'movie' | 'tv';
  season?: number;
  episode?: number;
  onClose?: () => void;
}

interface EpisodeData {
  episode_number: number;
  runtime?: number;
  name: string;
}

interface SeasonData {
  season_number: number;
  episode_count: number;
  episodes: EpisodeData[];
}

export default function VideoPlayer({
  mediaId,
  mediaType,
  season,
  episode,
  onClose
}: VideoPlayerProps) {
  const [embedUrl, setEmbedUrl] = useState<string>('');
  const [sources, setSources] = useState<VideoSource[]>([]);
  const [currentSourceIndex, setCurrentSourceIndex] = useState(0);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isTesting, setIsTesting] = useState(false);
  const [iframeKey, setIframeKey] = useState(0);

  // Episode tracking state
  const [currentSeason, setCurrentSeason] = useState<number | undefined>(season);
  const [currentEpisode, setCurrentEpisode] = useState<number | undefined>(episode);
  const [showTitle, setShowTitle] = useState<string>('');

  // TMDB metadata for auto-advance
  const [seasonData, setSeasonData] = useState<SeasonData | null>(null);
  const [totalSeasons, setTotalSeasons] = useState<number>(1);

  const iframeRef = useRef<HTMLIFrameElement>(null);
  const testTimeoutRef = useRef<NodeJS.Timeout | null>(null);
  const mutationObserverRef = useRef<MutationObserver | null>(null);
  const autoAdvanceTimerRef = useRef<NodeJS.Timeout | null>(null);
  const router = useRouter();
  const searchParams = useSearchParams();

  // Extract season/episode from URL params
  useEffect(() => {
    if (mediaType === 'tv') {
      const urlSeason = searchParams.get('season');
      const urlEpisode = searchParams.get('episode');
      
      if (urlSeason && urlEpisode) {
        const newSeason = parseInt(urlSeason);
        const newEpisode = parseInt(urlEpisode);
        
        if (newSeason !== currentSeason || newEpisode !== currentEpisode) {
          setCurrentSeason(newSeason);
          setCurrentEpisode(newEpisode);
        }
      }
    }
  }, [searchParams, mediaType, currentSeason, currentEpisode]);

  // Fetch TMDB episode metadata for auto-advance
  useEffect(() => {
    const fetchEpisodeMetadata = async () => {
      if (mediaType !== 'tv' || !currentSeason) return;

      const API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY?.trim();
      if (!API_KEY) return;

      try {
        // Get show details for total seasons
        const showResponse = await fetch(
          `https://api.themoviedb.org/3/tv/${mediaId}?api_key=${API_KEY}`
        );
        if (showResponse.ok) {
          const showData = await showResponse.json();
          setTotalSeasons(showData.number_of_seasons || 1);
          setShowTitle(showData.name || 'Unknown Show');
        }

        // Get season details with episodes
        const seasonResponse = await fetch(
          `https://api.themoviedb.org/3/tv/${mediaId}/season/${currentSeason}?api_key=${API_KEY}`
        );
        if (seasonResponse.ok) {
          const data = await seasonResponse.json();
          setSeasonData(data);
          console.log(`üì∫ Loaded season ${currentSeason} metadata:`, data.episodes?.length, 'episodes');
        }
      } catch (error) {
        console.warn('Failed to fetch episode metadata:', error);
      }
    };

    fetchEpisodeMetadata();
  }, [mediaId, mediaType, currentSeason]);

  // Auto-advance timer based on episode runtime
  useEffect(() => {
    if (mediaType !== 'tv' || !currentEpisode || !currentSeason) return;

    // Clear existing timer
    if (autoAdvanceTimerRef.current) {
      clearTimeout(autoAdvanceTimerRef.current);
    }

    // Get episode runtime from TMDB data
    const currentEpisodeData = seasonData?.episodes?.find(ep => ep.episode_number === currentEpisode);
    const episodeRuntime = currentEpisodeData?.runtime || 45; // Default 45 minutes
    const runtimeMs = episodeRuntime * 60 * 1000; // Convert to milliseconds

    console.log(`‚è±Ô∏è Setting auto-advance timer for ${episodeRuntime} minutes`);

    autoAdvanceTimerRef.current = setTimeout(() => {
      handleAutoAdvance();
    }, runtimeMs);

    return () => {
      if (autoAdvanceTimerRef.current) {
        clearTimeout(autoAdvanceTimerRef.current);
      }
    };
  }, [currentEpisode, currentSeason, seasonData, mediaId]);

  // Handle auto-advance to next episode
  const handleAutoAdvance = async () => {
    if (!currentEpisode || !currentSeason) return;

    let nextEpisode = currentEpisode + 1;
    let nextSeason = currentSeason;

    // Check if we need to advance to next season
    const episodeCount = seasonData?.episode_count || 20; // Default estimate
    if (nextEpisode > episodeCount) {
      nextEpisode = 1;
      nextSeason = currentSeason + 1;

      // Check if series is complete
      if (nextSeason > totalSeasons) {
        console.log('üé¨ Series complete - no more episodes');
        return;
      }
    }

    console.log(`üé¨ Auto-advancing: S${currentSeason}E${currentEpisode} ‚Üí S${nextSeason}E${nextEpisode}`);

    // Update state
    setCurrentSeason(nextSeason);
    setCurrentEpisode(nextEpisode);

    // Update URL
    router.replace(`/watch/tv/${mediaId}?season=${nextSeason}&episode=${nextEpisode}`, { scroll: false });

    // Force iframe reload with new episode
    setIframeKey(prev => prev + 1);
  };

  // MutationObserver to watch iframe src changes
  useEffect(() => {
    if (!iframeRef.current || mediaType !== 'tv') return;

    const iframe = iframeRef.current;
    
    mutationObserverRef.current = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        if (mutation.type === 'attributes' && mutation.attributeName === 'src') {
          const newSrc = iframe.src;
          handleIframeSrcChange(newSrc);
        }
      });
    });

    mutationObserverRef.current.observe(iframe, {
      attributes: true,
      attributeFilter: ['src']
    });

    return () => {
      if (mutationObserverRef.current) {
        mutationObserverRef.current.disconnect();
      }
    };
  }, [embedUrl, mediaType]);

  // Handle iframe src changes from auto-play
  const handleIframeSrcChange = (newSrc: string) => {
    if (!newSrc || mediaType !== 'tv') return;

    const { season: newSeason, episode: newEpisode } = parseSeasonEpisodeFromUrl(newSrc);
    
    if (newSeason && newEpisode && (newSeason !== currentSeason || newEpisode !== currentEpisode)) {
      console.log(`üé¨ Provider URL change detected: S${newSeason}E${newEpisode}`);
      
      // Reset auto-advance timer since provider is handling navigation
      if (autoAdvanceTimerRef.current) {
        clearTimeout(autoAdvanceTimerRef.current);
      }
      
      setCurrentSeason(newSeason);
      setCurrentEpisode(newEpisode);
      
      const newUrl = `/watch/tv/${mediaId}?season=${newSeason}&episode=${newEpisode}`;
      router.replace(newUrl, { scroll: false });
    }
  };

  // Test a source with timeout
  const testSource = (url: string, timeout: number = 8000): Promise<boolean> => {
    return new Promise((resolve) => {
      const iframe = document.createElement('iframe');
      iframe.style.display = 'none';
      iframe.style.position = 'absolute';
      iframe.style.top = '-9999px';
      iframe.src = url;

      let resolved = false;
      const timer = setTimeout(() => {
        if (!resolved) {
          resolved = true;
          iframe.remove();
          resolve(false);
        }
      }, timeout);

      iframe.onload = () => {
        if (!resolved) {
          resolved = true;
          clearTimeout(timer);
          iframe.remove();
          resolve(true);
        }
      };

      iframe.onerror = () => {
        if (!resolved) {
          resolved = true;
          clearTimeout(timer);
          iframe.remove();
          resolve(false);
        }
      };

      document.body.appendChild(iframe);
    });
  };

  // Update metadata from source
  const updateMetadataFromSource = (source: VideoSource) => {
    if (source.season && source.episode) {
      setCurrentSeason(source.season);
      setCurrentEpisode(source.episode);
    }
    if (source.title) {
      setShowTitle(source.title);
    }
  };

  // Find first working source
  const findWorkingSource = async (videoSources: VideoSource[]): Promise<number | null> => {
    setIsTesting(true);
    
    for (let i = 0; i < videoSources.length; i++) {
      console.log(`Testing source ${i + 1}/${videoSources.length}: ${videoSources[i].servers}`);
      
      const isWorking = await testSource(videoSources[i].url);
      
      if (isWorking) {
        console.log(`‚úì Source ${i + 1} working: ${videoSources[i].servers}`);
        updateMetadataFromSource(videoSources[i]);
        setIsTesting(false);
        return i;
      } else {
        console.log(`‚úó Source ${i + 1} failed: ${videoSources[i].servers}`);
      }
    }
    
    setIsTesting(false);
    return null;
  };

  // Load and test sources
  useEffect(() => {
    let cancelled = false;

    const loadSources = async () => {
      setLoading(true);
      setError(null);
      setEmbedUrl('');

      try {
        // Load sources from API
        const videoSources = mediaType === 'movie'
          ? await videoApi.getMovieSources(mediaId)
          : await videoApi.getTVSources(mediaId, currentSeason || 1, currentEpisode || 1);

        if (cancelled) return;

        if (videoSources.length === 0) {
          setError('No video sources available');
          setLoading(false);
          return;
        }

        setSources(videoSources);

        // Update metadata from first source
        if (videoSources[0]) {
          updateMetadataFromSource(videoSources[0]);
        }

        // Try server-side check first (if available)
        try {
          const response = await fetch('/api/check-embed', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
              mediaType,
              tmdbId: mediaId,
              season: currentSeason || 1,
              episode: currentEpisode || 1
            })
          });

          if (response.ok) {
            const data = await response.json();
            if (data.success && data.workingUrl) {
              const workingIndex = videoSources.findIndex(s => s.url === data.workingUrl);
              if (workingIndex !== -1) {
                setCurrentSourceIndex(workingIndex);
                setEmbedUrl(data.workingUrl);
                setIframeKey(prev => prev + 1);
                updateMetadataFromSource(videoSources[workingIndex]);
                setLoading(false);
                return;
              }
            }
          }
        } catch (err) {
          console.log('Server-side check failed, using client-side fallback');
        }

        // Client-side fallback
        const workingIndex = await findWorkingSource(videoSources);
        
        if (cancelled) return;

        if (workingIndex !== null) {
          setCurrentSourceIndex(workingIndex);
          setEmbedUrl(videoSources[workingIndex].url);
          setIframeKey(prev => prev + 1);
        } else {
          setError('All video sources failed to load');
        }

      } catch (err) {
        console.error('Failed to load video sources:', err);
        if (!cancelled) {
          setError('Failed to load video sources');
        }
      } finally {
        if (!cancelled) {
          setLoading(false);
        }
      }
    };

    if (mediaType === 'tv' && currentSeason && currentEpisode) {
      loadSources();
    } else if (mediaType === 'movie') {
      loadSources();
    }

    return () => {
      cancelled = true;
      if (testTimeoutRef.current) {
        clearTimeout(testTimeoutRef.current);
      }
    };
  }, [mediaId, mediaType, currentSeason, currentEpisode]);

  // Handle iframe error - try next source
  const handleIframeError = async () => {
    console.warn(`Iframe error on source: ${sources[currentSourceIndex]?.servers}`);
    
    const remainingSources = sources.slice(currentSourceIndex + 1);
    
    if (remainingSources.length === 0) {
      setError('All video sources failed to load');
      return;
    }

    // Test remaining sources
    for (let i = 0; i < remainingSources.length; i++) {
      const absoluteIndex = currentSourceIndex + 1 + i;
      const isWorking = await testSource(remainingSources[i].url, 5000);
      
      if (isWorking) {
        const newSource = sources[absoluteIndex];
        console.log(`Switching to source ${absoluteIndex + 1}: ${newSource.servers}`);
        setCurrentSourceIndex(absoluteIndex);
        setEmbedUrl(newSource.url);
        setIframeKey(prev => prev + 1);
        setError(null);
        updateMetadataFromSource(newSource);
        return;
      }
    }

    setError('All video sources failed to load');
  };

  // Handle successful load
  const handleIframeLoad = () => {
    console.log(`‚úì Successfully loaded: ${sources[currentSourceIndex]?.servers}`);
    setError(null);
    
    // Auto-detect episode changes on iframe load
    if (mediaType === 'tv' && iframeRef.current?.src) {
      const { season: newSeason, episode: newEpisode } = parseSeasonEpisodeFromUrl(iframeRef.current.src);
      
      if (newSeason && newEpisode && (newSeason !== currentSeason || newEpisode !== currentEpisode)) {
        console.log(`üé¨ Auto-detected via iframe load: S${newSeason}E${newEpisode}`);
        setCurrentSeason(newSeason);
        setCurrentEpisode(newEpisode);
        router.replace(`/watch/tv/${mediaId}?season=${newSeason}&episode=${newEpisode}`, { scroll: false });
      }
    }
  };

  // Handle close
  const handleClose = () => {
    // Clean up timers
    if (autoAdvanceTimerRef.current) {
      clearTimeout(autoAdvanceTimerRef.current);
    }
    
    if (onClose) {
      onClose();
    } else {
      router.back();
    }
  };

  // Handle ESC key
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Escape') {
        handleClose();
      }
    };

    document.addEventListener('keydown', handleKeyDown);
    return () => document.removeEventListener('keydown', handleKeyDown);
  }, []);

  // Get current episode info for display
  const currentEpisodeData = seasonData?.episodes?.find(ep => ep.episode_number === currentEpisode);
  const episodeRuntime = currentEpisodeData?.runtime;

  return (
    <div className="fixed inset-0 z-50 bg-black">
      {/* Loading State */}
      {(loading || isTesting) && (
        <div className="absolute inset-0 flex items-center justify-center">
          <div className="text-white text-center">
            <div className="animate-spin rounded-full h-16 w-16 border-4 border-white border-t-transparent mb-6 mx-auto"></div>
            <p className="text-xl mb-2">
              {isTesting ? 'Testing video sources...' : 'Loading video sources...'}
            </p>
            <p className="text-sm text-gray-400">
              Finding the best server for you
            </p>
            {sources.length > 0 && (
              <p className="text-xs text-gray-500 mt-2">
                {sources.length} sources available
              </p>
            )}
          </div>
        </div>
      )}

      {/* Error State */}
      {error && !loading && !isTesting && (
        <div className="absolute inset-0 flex items-center justify-center">
          <div className="text-white text-center max-w-md px-6">
            <div className="text-6xl mb-6">‚ö†Ô∏è</div>
            <h2 className="text-2xl font-bold mb-4">Unable to Play Video</h2>
            <p className="text-gray-300 mb-4">{error}</p>
            {sources.length > 0 && (
              <p className="text-sm text-gray-400 mb-6">
                Tested {sources.length} different servers
              </p>
            )}
            
            <button
              onClick={handleClose}
              className="px-8 py-3 bg-gray-700 hover:bg-gray-600 rounded-lg font-medium transition-colors"
            >
              Go Back
            </button>
          </div>
        </div>
      )}

      {/* Video Player */}
      {embedUrl && !loading && !error && !isTesting && (
        <>
          <iframe
            key={iframeKey}
            ref={iframeRef}
            src={embedUrl}
            className="w-full h-full"
            allowFullScreen
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
            onError={handleIframeError}
            onLoad={handleIframeLoad}
            style={{ border: 'none' }}
          />

          {/* Top Left - Back button + Show Title */}
          <div className="absolute top-4 left-4 z-30 flex items-center gap-3">
            <button
              onClick={handleClose}
              className="p-2 rounded-full bg-black/60 backdrop-blur-sm text-white hover:bg-black/80 transition-colors"
              aria-label="Back"
            >
              <ArrowLeft className="w-6 h-6" />
            </button>

            {showTitle && (
              <h1 className="text-white font-extrabold text-lg sm:text-xl truncate max-w-xs sm:max-w-md">
                {showTitle}
              </h1>
            )}
          </div>

          {/* Top Center - Dynamic Season/Episode Display */}
          {mediaType === 'tv' && currentSeason && currentEpisode && (
            <div className="absolute top-4 left-1/2 transform -translate-x-1/2 z-30">
              <div className="bg-black/60 backdrop-blur-sm text-white px-4 py-1 rounded-md shadow-lg">
                <span className="font-bold text-base sm:text-lg">
                  S{currentSeason}E{currentEpisode}
                </span>
                {episodeRuntime && (
                  <span className="text-xs ml-2 opacity-75">
                    ({episodeRuntime}min)
                  </span>
                )}
              </div>
            </div>
          )}

          {/* Top Right - ESC hint */}
          <div className="absolute top-4 right-4 text-white/70 text-sm pointer-events-none">
            {mediaType === 'tv' ? (
              <>Auto-advance enabled ({episodeRuntime || 45}min)</>
            ) : 'Auto-switch enabled'} ‚Äî Press ESC to exit
          </div>

          {/* Development Debug Info */}
          {process.env.NODE_ENV === 'development' && (
            <div className="absolute bottom-4 left-4 bg-black/70 text-white px-4 py-2 rounded-lg text-sm backdrop-blur-sm">
              <div className="font-mono">
                Server: {sources[currentSourceIndex]?.servers}
              </div>
              <div className="text-xs text-gray-300 mt-1">
                Source {currentSourceIndex + 1} of {sources.length}
              </div>
            </div>
          )}
        </>
      )}
    </div>
  );
}
